/**
 * Convert t "H Ni th " => "Ha_Noi_thu_do"
 * @param {*} str
 * @returns
 */
export function convertToSnakeCase(str) {
  const slug = removeAccents(str).replace(/\s+/g, "_");
  return slug;
}

